footer {
  background: var(--color-bg-variant);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.1rem;
  margin-top: 7rem;
}

.footer_socials {
  display: grid;

  justify-content: right;
  gap: 0.8rem;
  margin-bottom: 0.2rem;
}

.footer_socials a {
  background: var(--color-bg-variant);
  color: var(--color-primary);
  padding-right: 2rem;
}

.footer_socials .icon {
  font-size: 0.9rem;
}

