.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.aboute_me {
  margin-left: auto;
  width: 65%;
  aspect-ratio: 1/1;
  border-radius: 5rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.aboute_me img {
  border-radius: 5rem;
  overflow: hidden;
  opacity: 0.8;
  transform: rotate(10deg);
  transition: var(--transition);
}

.aboute_me img:hover {
  transform: rotate(0deg);
}

.about_content p {
  margin: 5rem 0 2.6rem;
  margin-right: 6rem;
}

@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .aboute_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about_content p {
    margin: 1rem 1.5rem;
    justify-content: center;
  }
}

@media screen and (max-width: 600px) {
  .aboute_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_content {
    font-size: 1rem;
    text-align: center;
    margin: 1rem 0;
  }
}
